<template>
  <v-container>
    <v-layout row v-if="response.message !== null">
      <v-flex xs12>
        <app-alert @dismissed="onDismissed" :response="response" class="mb-4"></app-alert>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12 lg10 offset-lg1>
        <v-layout row>
          <v-flex xs12 class="text-xs-center">
            <h3 class="primary--text">Termo de Aceite</h3>
          </v-flex>
        </v-layout>
        <v-layout 
          style="max-height: 500px"
          class="scroll-y mt-4"
          id="scroll-target">
          <v-flex
            class="mt-4"
            v-scroll="{
              target: '#scroll-target',
              callback: onFinishRead
            }">
            <h5>Poltica de Uso e Termo de Aceite</h5>
            <p>A brincadeira de controle de sorvetes, o IceScream,  organizada, desenvolvida e administrada pelos participantes ativos do <b class="primary--text">CED  Centro de Estudos Dart</b>, tendo carter educacional e de desenvolvimento pessoal, sendo assim, no possui vnculo empregatcio, nenhuma relao trabalhista, social ou mesmo econmica com a Dart Digital e segue os termos e regras atribudos ao <b class="primary--text">CED  Centro de Estudos Dart</b>.
            <br>A brincadeira  facultativa, no possui carter pejorativo ou discriminatrio e no poder ter nenhum custo financeiro a nenhum participante.
            <br>Os administradores e participantes concordam com a preservao do sigilo e dados pessoais de todos os participantes, no podendo divulgar as informaes que nele contiver.
            <br>A saber, <b class="primary--text">Administrador</b>  o participante responsvel por validar, avaliar e excluir participantes e ser escolhido por votao anualmente. O quadro de administradores dever ser composto por no mnimo 20% do total de participantes ativos.</p>
            <p class="mt-2">Ao aceitar participar do IceScream, o participante concorda com os seguintes termos, sabendo que, a qualquer momento, poder sair da brincadeira.</p>
            <p>1. O participante cadastrado no aplicativo, dever aceitar os termos de Poltica de Uso e Termo de Aceite para utilizar a aplicao e participar do Ice Scream;</p>
            <p>2. A quantidade de sorvete dever ser de 200 ml de sorvete para cada participante ativo;</p>
            <p>3. O sabor poder ser escolhido pelo devedor;</p>
            <p>4. O pagamento dever ser feito at 15 dias aps o evento (ingresso ou aniversrio);</p>
            <p>5. Passados 15 dias o participante inadimplente ser bloqueado at o pagamento e aps 30 dias ser excludo da brincadeira caso haja pendncias;</p>
            <p>6. Participantes excludos precisam aguardar 06 meses caso queiram voltar  brincadeira;</p>
            <p>7. Trocas sero permitidas desde que os administradores aprovem;</p>
            <p>8. O participante aceita que sua imagem seja divulgada no aplicativo;</p>
            <p>9. O participante aceita que seja avaliado de acordo com o sorvete comprado (sabor, marca, quantidade e adicionais);</p>
            <p>10. Todas as decises, regras e demais assuntos que no constarem nesta Politica de Uso e Termo de Aceite, sero tratadas em reunies convocadas pelo CED.</p>
          </v-flex>
        </v-layout>
        <v-layout row v-if="!user.acceptedTerms">
          <v-flex xs12>
            <v-checkbox v-model="accepted" dark color="primary" :label="checkboxLabel" class="mt-4"></v-checkbox>
          </v-flex>
        </v-layout>
        <v-layout row v-if="!user.acceptedTerms">
          <v-flex xs12 class="text-xs-right">
            <v-btn 
              class="primary" 
              :disabled="!readed || !accepted"
              :loading="loading"
              @click="onAcceptTerms">
              <span slot="loader" class="custom-loader">
                <v-icon dark>cached</v-icon>
              </span>
              Continuar
            </v-btn>        
          </v-flex>
        </v-layout>
        <v-layout row v-if="user.acceptedTerms">
          <v-flex xs>
            <p class="primary--text mt-5" style="font-size: 1.3em;">Lido e aceito em {{user.acceptedTermsDate | date}}</p>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        checkboxLabel: 'Li e aceito os termos',
        readed: false,
        accepted: false
      }
    },
    computed: {
      user () {
        return this.$store.getters.user
      },
      response () {
        return this.$store.getters.response
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    methods: {
      onFinishRead (e) {
        let scrollHeight = e.target.scrollHeight - e.target.clientHeight
        if (!this.readed && scrollHeight === e.target.scrollTop) {
          this.readed = true
        }
      },
      onAcceptTerms () {
        this.$store.dispatch('acceptTerms', this.user)
          .then(() => {
            this.$router.push('/')
          })
      },
      onDismissed () {
        this.$store.dispatch('clearResponse')
      }
    }
  }
</script>


